import { LocalStorageService } from './../../core/service/local-storage.service';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { HttpSuccesFailureResponse } from "../../core/http_wrapper/http_wrapper_response";
import { HttpUserAuthUtilsService } from "../../core/http_wrapper/http-userauth-utils.service";
import { SearchResultResponse } from "../../core/models/searchResultResponse";
import { Constants, MixpanelEvent } from '../../core/constants';
import { Router } from '@angular/router';
import { Subscription } from "rxjs";
import { GlobalSubscriptionService } from "app/globalSubcription.service";
import { MixpanelService } from '../../core/mixpanelService.service';


@Component({
  selector: 'app-searchtrainers',
  templateUrl: './searchtrainers.component.html',
  styleUrls: ['./searchtrainers.component.css']
})
export class SearchtrainersComponent implements OnInit, HttpSuccesFailureResponse {
  term: string = '';
  searchResultResponse: SearchResultResponse[] = [];
  searchtrainercounting: number = 0;
  trainerListisLoading: boolean = false;
  isLoaded: boolean = false;
  private subscription: Subscription;
  @Output() searchtrainercount = new EventEmitter();

  constructor(private myhttp: HttpUserAuthUtilsService, private router: Router, private mixpanel: MixpanelService,
    private globalSubscriptionService: GlobalSubscriptionService) { }

  ngOnInit() {
    this.subscription = this.globalSubscriptionService.serachclassState
      .subscribe((term) => {
        this.term = localStorage.getItem("term");
        this.trainerListisLoading = true;
        this.myhttp.searchTrainerByType("trainer", this.term, LocalStorageService.getUserId(), this, this.trainerListisLoading)
      });
    this.term = localStorage.getItem("term");
    this.trainerListisLoading = true;
    this.myhttp.searchTrainerByType("trainer", this.term, LocalStorageService.getUserId(), this, this.trainerListisLoading)
  }
  onSuccess(type: any, responsedata: any) {
    if (type === Constants.showtrainersearchByType) {
      this.trainerListisLoading = false;
      this.searchResultResponse = responsedata.data;
      this.searchtrainercounting = this.searchResultResponse.length
      this.searchtrainercount.next(this.searchtrainercounting)
    }

  }
  onFailure(type: any, response: string) {
    this.trainerListisLoading = false;
  }
  gotoTrainerDetail(value) {
    sessionStorage.setItem("trainer_id", value.id + "")
    this.mixpanel.logEventWithAnalytics(MixpanelEvent.visitTrainerProfile, { 'origin': 'search', 'Source' : 'web' });
    this.router.navigate(['/trainer', value.id, value.firstName.replace(/ /g, '')]);

  }

}



