import { MixpanelEvent } from './../../core/constants';
import { MixpanelService } from './../../core/mixpanelService.service';
import { GlobalSubscriptionService } from './../../app/globalSubcription.service';
import { HttpCommonUtilsService } from './../../core/http_wrapper/http-common-utils.service';
import { LocationList } from './../../core/models/filterClassRequest';
import { ClassDetail } from './../../core/models/classListResponse';
import { Router } from '@angular/router';
import { ModelpopupLrComponent } from '../../loginRegister/modelpopup-lr/modelpopup-lr.component';
import { JoinclasspopupComponent } from '../../joinclasspopup/joinclasspopup.component';
import { LocalStorageService } from '../../core/service/local-storage.service';
import { Constants } from '../../core/constants';
import { Component, OnInit, EventEmitter, Output, AfterViewChecked, Input } from '@angular/core';
import { HttpSuccesFailureResponse } from "../../core/http_wrapper/http_wrapper_response";
import { HttpUserAuthUtilsService } from "../../core/http_wrapper/http-userauth-utils.service";
import { SearchResultResponse } from "../../core/models/searchResultResponse";
import { overlayConfigFactory, Modal } from 'angular2-modal';
import { BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { Subscription } from 'rxjs/Subscription';
import { UserSession } from 'core/sessions';
import { ConfirmpopupComponent } from 'common/confirmpopup/confirmpopup.component';


@Component({
  selector: 'app-searchclasses',
  templateUrl: './searchclasses.component.html',
  styleUrls: ['./searchclasses.component.css']
})
export class SearchclassesComponent implements OnInit, HttpSuccesFailureResponse, AfterViewChecked {
  term: string;
  searchResultResponse: SearchResultResponse[] = [];
  searchclassescount: number = 0;
  usertype: number;
  classesList: ClassDetail[] = [];
  listSize: number;
  locatonlist: LocationList[] = [];
  filterActivitiesList: number[] = [];
  filterTimeList: string[] = [];
  activeindex: number = 0;
  mSelectedDate: string;
  mSelectedDay: any;
  currentPage: number = 1;
  isLoadMoreData: boolean = true;
  lastScrollDistance: number = 0;
  classListisLaoding: boolean = false;
  isShowLoader: boolean = true;
  _filterContent: any;
  isLoaded: boolean = false;
  classSessionIndex: number = 0;
  isTrainerMapped: boolean = false;

  body: any;
  currency:any;

  @Output() public updateclassescount = new EventEmitter();
  @Input() public searchResponse: any[] = [];
  private subscription: Subscription;
  public atGymText = UserSession.getDynamicElements(Constants.AtGymText)
  public isPrime = UserSession.getDynamicElements(Constants.isPrime)=="Yes"?true:false;
  wishListClassName: string;
  wishListClassSessionId: any;

  constructor(private globalSubscriptionService: GlobalSubscriptionService, private mixpanel: MixpanelService,
     private myhttp: HttpUserAuthUtilsService, private common_http: HttpCommonUtilsService,
     private modal: Modal, private router: Router) { }

  ngOnInit() {
    this.currency = LocalStorageService.getCurrencyCode();

    this.subscription = this.globalSubscriptionService.serachState
      .subscribe((term) => {
        this.term = localStorage.getItem("term");
        this.myhttp.searchClasses(this.term, this, true);
      });
    this.globalSubscriptionService.ClassListState
      .subscribe((value) => {
        this.refreshList(value);
      });
      this.globalSubscriptionService.waitListState
      .subscribe((value) => {
        this.refreshListWait(value);
      });
    this.usertype = LocalStorageService.getUserType();
    this.term = localStorage.getItem("term");
    this.classListisLaoding = true;
    this.myhttp.searchClasses(this.term, this, true);
    if (LocalStorageService.getIsTrainerMapped() == "true") {
      this.isTrainerMapped = true;
    }

    //this.common_http.getClassesList(this.filterContent, this,this.isShowLoader);


  }



  onSuccess(type: any, responsedata: any) {
    if (type == Constants.ClassSearch) {
      this.classListisLaoding = false;
      this.classesList = responsedata.data;
      if(this.isPrime){
        this.classesList.forEach(element => {
            element.credit=undefined;
       
        });
      }
      
      this.searchclassescount = this.classesList.length;
      this.updateclassescount.next(this.searchclassescount);
      this.classesList.forEach(element => {
        element.isSpecialProgram = UserSession.isSpecialProgramClass(element);
        if(element.showAvailableSeatType&&element.showAvailableSeatType=='SHOWTHREEANDABOVEAVAILSEAT'&&element.availableSeat>3){
          element.availableSeatText="+3";
        }else{
          element.availableSeatText=element.availableSeat;

        }
      });
    }
    if (type === Constants.AddToWaitList) {
  
      this.openconfirmpopup(LocalStorageService.getRefundValue().waitListPopUpMessage, 'Success');
    
      this.globalSubscriptionService.onWaitListListRefresh(this.wishListClassSessionId);
    }


  }
  openconfirmpopup(message, title) {
    return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title }, BSModalContext));
  }
  onFailure(type: any, response: string) {
    this.classListisLaoding = false;
  }
  joinclasspopup(classdetail, i) {
    if(classdetail.availableSeat==0&&!classdetail.userJoinStatus){
      this.addtowaitlist(classdetail);
      return;


    }
    this.classSessionIndex = i;
    return this.modal.open(JoinclasspopupComponent, overlayConfigFactory({ classdetail }, BSModalContext));


  }
  addtowaitlist(classdetail: ClassDetail) {
    if (this.isLoggedIn()) {
      this.wishListClassName = classdetail.title;
      this.wishListClassSessionId = classdetail.classSessionId;
      if(!classdetail.userJoinStatus){
         
        if(LocalStorageService.getUser()!=null&&LocalStorageService.getUser().data!=null&&!LocalStorageService.getUser().data.mobile){
          let message  = "You need to add your mobile number before adding the class to waitlist.";
          let title = "Add Phone";
          let parent = this;
          let errorCode = "WAITLISTERR"
          return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title , parent, errorCode}, BSModalContext));
  
        }

      this.myhttp.addClassToWaitList(LocalStorageService.getUserId(), classdetail.classSessionId, this, true);
    }
    } else {
      return this.modal.open(ModelpopupLrComponent, overlayConfigFactory({ num1: 2, num2: 3 }, BSModalContext));

    }
  }
  isLoggedIn() {
    return UserSession.isLoggedIn();
  }
  editClass(value) {
    sessionStorage.setItem("class_id", value.classId + "");
    this.router.navigate(['class', value.classId])
  }

  ngAfterViewChecked() {
    //this.common_http.getClassesList(this._filterContent, this,false);


  }
  @Input()
  set filterContent(body: any) {
    this._filterContent = body
  }
  get filterContent() {
    return this._filterContent;
  }
  navigateto(value) {
    if (this.usertype == 1) {
      sessionStorage.setItem("gym_id", value.gymId);
      sessionStorage.setItem("branch_id", value.branchId);
      this.mixpanel.logEventWithAnalytics(MixpanelEvent.visitGymProfile, {'origin': 'search' , 'Source' : 'web'});
      this.router.navigate(['gym', value.gymId, value.studioName.replace(/ /g, '')])
    }

  }
  refreshList(value) {
    for (let i = 0; i < this.classesList.length; i++) {
      if (this.classesList[i].classSessionId == value) {
        this.classesList[i].userJoinStatus = true;
        this.classesList[i].availableSeat = this.classesList[i].availableSeat - 1;
        break;
      }
    }

  }
  refreshListWait(value) {
    try {
    for (let i = 0; i < this.classesList.length; i++) {
        if (this.classesList[i].classSessionId == value) {
          this.classesList[i].wishType = 'WAITLIST';
          break;
        }
      }

  } catch (e) {
    console.log(e)
  }
  }
  addLocation(classDetail: ClassDetail, event: Event) {
    window['isNavigatedFromList'] = true;
    event.stopPropagation();
    event.preventDefault();
    this.router.navigate(['class', classDetail.classId]);
  }
}



