import { LocalStorageService } from './../../core/service/local-storage.service';
import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { HttpSuccesFailureResponse } from "../../core/http_wrapper/http_wrapper_response";
import { HttpUserAuthUtilsService } from "../../core/http_wrapper/http-userauth-utils.service";
import { Constants } from '../../core/constants';
import { SearchGymResultResponse } from "../../core/models/searchResultGymResponse";
import { Router } from '@angular/router';
import { Subscription } from "rxjs";
import { GlobalSubscriptionService } from "app/globalSubcription.service";


@Component({
  selector: 'app-searchgyms',
  templateUrl: './searchgyms.component.html',
  styleUrls: ['./searchgyms.component.css']
})
export class SearchgymsComponent implements OnInit,HttpSuccesFailureResponse  {
  term:string='';
  searchResultResponse: SearchGymResultResponse[]=[];
  searchgymcounting:number=0;
  gymListisLaoding:boolean=false;
  isLoaded:boolean=false;
  private subscription: Subscription;
  @Output() searchgymcount = new EventEmitter();

  constructor(private myhttp:HttpUserAuthUtilsService,private router:Router,private globalSubscriptionService:GlobalSubscriptionService) { }

  ngOnInit() {
    this.subscription = this.globalSubscriptionService.serachgymState
            .subscribe((term) => {
        this.term=localStorage.getItem("term");
        this.gymListisLaoding=true;  
        this.myhttp.searchTrainerByType("gym",this.term,LocalStorageService.getUserId(),this,true)              
            });
        this.term=localStorage.getItem("term");
        this.gymListisLaoding=true;
        this.myhttp.searchTrainerByType("gym",this.term,LocalStorageService.getUserId(),this,true)
  }
   onSuccess(type: any, responsedata: any) {
     this.gymListisLaoding=false;
      if(type == Constants.showtrainersearchByType){
       this.searchResultResponse=responsedata.data;
       this.searchgymcount.next(this.searchResultResponse.length)
     }

}
  onFailure(type: any, response: string) {
    this.gymListisLaoding=false;
  }
  gotoGymDetail(value){
    sessionStorage.setItem("gym_id",value.id+"")
    this.router.navigate(['gym', value.id, value.studioName.replace(/ /g, '')]);

}

}



