import { LocalStorageService } from './../core/service/local-storage.service';
import { GlobalSubscriptionService } from './../app/globalSubcription.service';
import { Subscription } from 'rxjs/Subscription';
import { Component, OnInit } from '@angular/core';
import { ClassDetail } from '../core/models/classListResponse';
import { Day } from '../core/models/futuredatesmodel';
import { LocationList } from '../core/models/filterClassRequest';
import { Constants, SeoElements } from '../core/constants';
import { HttpCommonUtilsService } from '../core/http_wrapper/http-common-utils.service';
import { UserSession } from '../core/sessions';
import { ActivatedRoute, Router } from '@angular/router';
import { Meta } from '@angular/platform-browser';

declare var $: any;

@Component({
  selector: 'app-searchresults',
  templateUrl: './searchresults.component.html',
  styleUrls: ['./searchresults.component.css']
})
export class SearchresultsComponent implements OnInit {
  searchclasscount: any = '0';
  searchtrainercount: any = '0';
  searchgymcount: any = '0';
  private subscription: Subscription;
  days: Day[];
  classesList: ClassDetail[] = [];
  listSize: number;
  locatonlist: LocationList[] = [];
  filterActivitiesList: number[] = [];
  filterTimeList: string[] = [];
  filterFitnessLevelList: any[] = [];
  filterpriceModelList: any[] = [];
  activeindex = 0;
  mSelectedDate: string;
  mSelectedDay: any;
  currentPage = 1;
  isLoadMoreData = true;
  lastScrollDistance = 0;
  classListisLaoding = false;
  body: any;
  userType: any;
  isTrainerCoutAvailable = false;
  isClassCountAvailable = false;
  isGymsCountAvailable = false;
  private sub: any;
  queryString: any;
  public isPrime = UserSession.getDynamicElements(Constants.isPrime)=="Yes"?true:false;


  constructor(private router: Router, private route: ActivatedRoute, private globalSubscriptionService: GlobalSubscriptionService,
    private common_http: HttpCommonUtilsService, private meta: Meta) {
      if(!this.isPrime){

      this.meta.updateTag({name: SeoElements.title, content: SeoElements.searchresultMetaTitle})
      this.meta.updateTag({name: SeoElements.description, content: SeoElements.searchresultDescription})
      this.meta.updateTag({name: SeoElements.keywords, content: SeoElements.searchresultKeywords})
    }
     }
ngAfterViewInit() {

}
  ngOnInit() {
      this.sub = this.route.params.subscribe(params => {
      this.queryString = params['query'] || null;
      if (this.queryString != null) {
        localStorage.setItem('term', this.queryString)
      } else {
        this.router.navigate(['/'])
      }

    });
      this.getclasseslist(null);
      this.userType = LocalStorageService.getUserType();

  }
  classescount(event) {
    this.isClassCountAvailable = true;
    this.searchclasscount = event + '';
    this.changenavTabs()

  }
  trainercount(event) {
    this.isTrainerCoutAvailable = true;
    this.searchtrainercount = event + '';
    this.changenavTabs()

  }
  gymcount(event) {
    this.isGymsCountAvailable = true;
    this.searchgymcount = event + '';
    this.changenavTabs();


  }
   getclasseslist(value) {
    { { value === null ? this.activeindex = 0 : this.activeindex = value.index } };
    { { value === null ? this.mSelectedDate = UserSession.getcurrentdate() : this.mSelectedDate = value.year + '-' + value.month_number + '-' + value.daynum } };
       { { value === null ? this.mSelectedDay = UserSession.getcurrentday() : this.mSelectedDay = UserSession.getday(value.day) } };

    this.createRequestForClass(true);
  }
   HandleFilterLocation(event) {
    this.resetBeforeLoad();
    this.locatonlist = event;
    this.createRequestForClass(true);
  }
  HandleFilterActivities(event) {
    this.resetBeforeLoad();
    this.filterActivitiesList = event;
    this.createRequestForClass(true);

  }
  HandleFilterTime(event) {
    this.resetBeforeLoad();
    this.filterTimeList = event;
    this.createRequestForClass(true);

  }

  HandleFilterFitnessLevel(event) {
    this.resetBeforeLoad();
    this.filterFitnessLevelList = event;
    this.createRequestForClass(true);
  }
  HandleFilterpriceModel(event) {
    this.resetBeforeLoad();
    this.filterpriceModelList = event;
    this.createRequestForClass(true);
  }
  resetBeforeLoad() {
        this.currentPage = 1;
        this.isLoadMoreData = true;
        this.classesList = [];

  }
  createRequestForClass(isShowLoader: boolean) {
    this.classListisLaoding = true;
        if (isShowLoader) {
     this.resetBeforeLoad();
    }
    this.body = { 'classDate': this.mSelectedDate, 'day': this.mSelectedDay, 'locationList': this.locatonlist, 'activityList': this.filterActivitiesList, 'timingList': this.filterTimeList,'fitnessLevelList':this.filterFitnessLevelList,'priceModelList':this.filterpriceModelList , 'pageSize': Constants.pageSize, 'pageNumber': this.currentPage };
    this.getAllClassesListWithFilter(this.body, isShowLoader);
  }
  getAllClassesListWithFilter(body, isShowLoader: boolean) {
    // this.common_http.getClassesList(body, this,isShowLoader);
  }
  onSuccess(type: any, responsedata: any) {
    if (type == Constants.GetClassesApi) {
      this.classListisLaoding = false;

    const temp_list: ClassDetail[] = responsedata.data;
    const temp_length: number = this.classesList.length
   // this.classesList = responsedata.data;
   temp_list.forEach(element => {
     this.classesList.push(element);
   });
    this.listSize = this.classesList.length;
      this.classesList.forEach(element => {
        try {element.fromTimeInString = element.fromTimeInString.split(' ')[0]; } catch (e) {}

        });
      if (temp_list.length === 0) {
        this.isLoadMoreData = false;
      } else {
        this.isLoadMoreData = true;
      }
}
  }
  onFailure(type: any, response: string) {
     if (type == Constants.GetClassesApi) {
       this.classListisLaoding = false;
       if (this.currentPage == 1) {
       this.classesList = [];
                        }
       this.listSize = this.classesList.length;
      }
  }

  changenavTabs() {
    if (this.isClassCountAvailable && this.isTrainerCoutAvailable && this.isGymsCountAvailable) {
  if (+this.searchclasscount > 0 ) {
$(document).ready(function() {
      activaTab('searchclasses');
});
  } else if (+this.searchgymcount < 1 && +this.searchclasscount < 1 && +this.searchtrainercount > 0) {
$(document).ready(function() {
      activaTab('searchtrainers');
});
  } else if (+this.searchtrainercount < 1 && +this.searchclasscount < 1 && +this.searchgymcount > 0) {
$(document).ready(function() {
      activaTab('searchgyms');
}); }

    }
    function activaTab(tab) {
  $('.nav-tabs a[href="#' + tab + '"]').tab('show');
};
  }

}



